<template>
  <div class="va-chat">
    <div
      class="va-chat__body"
      :style="{ height: height, backgroundColor: '#00000033' }"
      v-sticky-scroll="{
        animate: true,
        duration: 200,
      }"
    >
      <div
        class="va-chat__message"
        v-for="(message, index) in value"
        :key="index"
        :style="message.yours ? computedYoursStyle : computedStyle"
        :class="{ 'va-chat__message--yours': message.yours }"
      >
        <span class="va-chat__message-text">
          {{ message.text }}
        </span>
      </div>
    </div>
    <div class="va-chat__controls">
      <va-input
        :autosize="true"
        v-model="inputMessage"
        @keypress.enter="sendMessage"
        :placeholder="$t('dashboard.profile.tabs.support.placeholder')"
        class="va-chat__input"
      />
      <va-button @click="sendMessage">
        {{ $t('dashboard.profile.tabs.support.send') }}
      </va-button>
    </div>
  </div>
</template>

<script>
import { StickyScroll } from '../../../services/vuestic-ui/components'

export default {
  name: 'chat',
  directives: { StickyScroll },
  data() {
    return {
      inputMessage: '',
    }
  },
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkForeground }
    },
    computedYoursStyle() {
      return { backgroundColor: this.$themes.info }
    },
  },
  props: {
    value: {
      type: Array,
      default: () => [
        {
          text:
            'Hello! So glad you liked my work. Do you want me to shoot you?',
          yours: false,
        },
        {
          text: 'Yeah, that would be cool. Maybe this Sunday at 3 pm?',
          yours: true,
        },
        {
          text: 'Sounds great! See you later!',
          yours: false,
        },
        {
          text: 'Should I bring a lightbox with me?',
          yours: true,
        },
        {
          text:
            'No, thanks. There is no need. Can we set up a meeting earlier?',
          yours: false,
        },
        {
          text: "I'm working on Vuestic, so let's meet at 3pm. Thanks!",
          yours: true,
        },
      ],
    },
    height: {
      default: '20rem',
      type: String,
    },
  },
  methods: {
    sendMessage() {
      if (!this.inputMessage) {
        return
      }
      this.$emit('input', {
        text: this.inputMessage,
        yours: true,
      })
      this.inputMessage = ''
    },
  },
}
</script>

<style lang="scss">
.chat {
  &__content {
    @include va-flex-center();
  }
}

$chat-message-br: 0.875rem;

.va-chat {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr 2.5rem;
  padding: 0;

  &__body {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    overflow-y: auto;
    padding: 20px 10px;
  }

  &__message {
    position: relative;
    padding: 0.657rem 1.375rem;
    margin-bottom: 0.625rem;
    max-width: 70%;
    overflow-wrap: break-word;
    border-radius: 0.5rem;
    border-top-left-radius: 0;
    align-self: flex-start;
    background-color: $light-gray2;

    &-text {
      display: block;
      transform: translateY(-2px);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--yours {
      align-self: flex-end;
      border-top-right-radius: 0;
      border-top-left-radius: 0.5rem;
    }
  }

  &__controls {
    display: flex;
    align-items: center;
    height: 2rem;
    padding: 2.6rem 0;
  }

  &__input {
    margin-bottom: 0;
  }
}
</style>
